import { Container, Row, Col, Image, Card, Pagination, Button } from "react-bootstrap";
import { Transition } from "react-transition-group";
import { useParams, useLocation } from "react-router-dom";
import ReactAudioPlayer from "react-audio-player";
import React, { useState } from "react";
import { get_data } from "../data/get_data";

const duration = 500;

const defaultStyle = {
  transition: `opacity ${duration}ms ease-in-out`,
  opacity: 0,
};

const transitionStyles = {
  entering: { opacity: 1 },
  entered: { opacity: 1 },
  exiting: { opacity: 0 },
  exited: { opacity: 0, display: "none", },
};

function useQuery() {
  const { search } = useLocation();
  return React.useMemo(() => new URLSearchParams(search), [search]);
}

function getPaginationItems(questions, item) {
  const paginationItems = [];

  questions.forEach((_, qid) => {
    const link = "?item=" + (qid + 1);
    paginationItems.push(
      <Pagination.Item
        key={qid + 1}
        active={qid + 1 === parseInt(item)}
        href={link}
      >
        {qid + 1}
      </Pagination.Item>
    );
  });

  return paginationItems;
}

export default function Topic(props) {
  const { topic_name } = useParams();
  const item = useQuery().get("item");

  const data = get_data(props.chapter, topic_name);
  const paginationItems = getPaginationItems(data.questions, item);

  return (
    <Container fluid>
      <Row className="pb-5">
        <Col>
          {item === null ? (
            <TopicIntroPage
              title={data.quizTitle}
              synopsis={data.quizSynopsis}
            />
          ) : (
            <TopicPage question={data.questions[item - 1]} />
          )}
        </Col>
      </Row>
      <Row className="pt-5">
        <Col>
          <Pagination className="justify-content-center fixed-bottom">
            {paginationItems}
          </Pagination>
        </Col>
      </Row>
    </Container>
  );
}

function TopicIntroPage(props) {
  return (
    <Row>
      <Col>
        <h5>{props.title}</h5>
        <p>{props.synopsis}</p>
      </Col>
    </Row>
  );
}

function TopicPage(props) {
  const [showAnswer, setShowAnswer] = useState(false);

  if (props.question.answerPic === "") {
    props.question.answerPic = props.question.questionPic;
  }

  return (
    <Row onClick={() => setShowAnswer(true)}>
      <Col
        xs={{ span: 12, order: 2 }}
        sm={{ span: 12, order: 2 }}
        md={{ span: 12, order: 2 }}
        lg={{ span: 7, order: 1 }}
        xl={{ span: 7, order: 1 }}
      >
        <Card >
          <Card.Body className="h-auto">
            <Card.Title>
              <ReactAudioPlayer src={props.question.questionAudio} controls />
            </Card.Title>
            <Card.Text>{props.question.question}</Card.Text>
          </Card.Body>
          <hr />

          <Card.Body className="h-75 overflow-auto">
          
          

          <Transition in={!showAnswer} timeout={50}>
              {(state) => (
                <div style={{...defaultStyle, ...transitionStyles[state] }}>
                  <Button variant="outline-dark" size="lg">What's the Answer?</Button>
                </div>
              )}
            </Transition>

            <Transition in={showAnswer} timeout={500}>
              {(state) => (
                <div style={{ ...defaultStyle, ...transitionStyles[state] }}>
                  <Card.Title>
                    <ReactAudioPlayer
                      src={props.question.answerAudio}
                      controls
                    />
                  </Card.Title>
                  <Card.Text>{props.question.explanation}</Card.Text>
                </div>
              )}
            </Transition>
          </Card.Body>
        </Card>
      </Col>
      <Col
        xs={{ span: 12, order: 1 }}
        sm={{ span: 12, order: 1 }}
        md={{ span: 12, order: 1 }}
        lg={{ span: 5, order: 2 }}
        xl={{ span: 5, order: 2 }}
      >
        {showAnswer ? (
          <Image src={props.question.answerPic} rounded className="w-100" />
        ) : (
          <Image src={props.question.questionPic} rounded className="w-100" />
        )}
      </Col>
    </Row>
  );
}
