import { Row, Col, Media, Image } from 'react-bootstrap';


function Header() {
    return (
        <div>
            <h5>
                АЗБУКА ОРИЕНТИРОВАНИЯ, А ТАКЖЕ УМЕНИЕ ДЕИСТВОВАТЬ
                ВЕЗДЕ ВСЕГДА И В ЛЮБЫХ СИТУАЦИЯХ (ЗНАНИЕ - СИЛА!)
            </h5>
            <br />
            <h6>
                О чём эта программа? И для кого?
            </h6>
        </div>
    )
}

function Content() {
    return (
        <div>
            <p>
                Об умении ориентироваться по карте и местности с применением
                и без применения технических средств, об умении определять
                время без часов и предсказывать погоду без приборов.
                Она научит Вас ориентироваться в созвездиях, разбираться в следах,
                растениях, грибах, действовать в различных ситуациях на суше и на воде.
                Для любителей путешествий, туристов, рыбаков и охотников,
                грибников и любителей природы, краеведов, скаутов, юных следопытов.
                Для школьников, как дополнение
                к курсу “Основы безопасности жизнедеятельности”.
                <br /><br />


                Порядок работы с программой
                Выбираем режим обучения или теста, кликая на соответствующую вкладку.
                На открывшейся странице описаны особенности работы в данном режиме.
                Из меню программы выбираем интересующую Вас тему.
                Внизу перечислены порядковые номера вопросов данной темы.
                Переходите от одного вопроса к другому, последовательно кликая на них.
      
            </p>
        </div>
    )
}


export function Home(props) {
    return (
        <Media>
            <Row>
                <Col xs={12} sm={12} md={8} lg={8} xl={8}>
                    <Media.Body>
                        <Header />
                        <Content />
                    </Media.Body>
                </Col>
                <Col xs={12} sm={12} md={4} lg={4} xl={4}>
                    <Image src="/images/палатка2.jpg" roundedCircle className="w-100"/>
                </Col>
            </Row>
        </Media>
    );
}

export default Home;
