import "./App.css";
import { Container, Row, Col } from "react-bootstrap";
import { Route, Switch } from "react-router-dom";

import Navigation from "./components/navigation/Navigation";
import Home from "./components/home/Home";
import Learning from "./components/learning/Learning";
import Testing from "./components/testing/Testing";

// This is the main routing switch
function PrimaryRouterSwitch() {
  return (
    <Switch>
      <Route exact path="/">
        <Home />
      </Route>
      <Route path="/learning">
        <Learning />
      </Route>
      <Route path="/testing">
        <Testing />
      </Route>
      <Route path="/:id">
        <p>Error!!</p>
      </Route>
    </Switch>
  );
}

// This is the main application
function App() {
  return (
    <Container className="App">
      <Row>
        <Col>
          Welcome to the site!!
        </Col>
      </Row>
      <Row className="pb-2">
        <Col>
          <Navigation />
        </Col>
      </Row>
      <PrimaryRouterSwitch />
    </Container>

  );
}

export default App;
