import { useParams } from "react-router-dom";

import Quiz from "../react-quiz-component/Quiz";
import { get_data } from "../data/get_data";

export function Topic(props) {
  const { topic_name } = useParams();
  const quiz = get_data(props.chapter, topic_name);

  return <Quiz quiz={quiz} shuffle={true} showInstantFeedback={true} />;
}

Topic.defaultProps = {
  chapter: "DEFAULT_UNKNOWN",
};

export default Topic;
